import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import locationType from 'components/__types__/location';
import LocationFieldTable from 'components/location-field-table/LocationFieldTable';

const LocationDetails = ({ t, location }) => {
  return (
    <Box>
      <h3>
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Location',
        })}
      </h3>
      <LocationFieldTable location={location} />
    </Box>
  );
};

LocationDetails.propTypes = {
  location: locationType,
  t: PropTypes.func.isRequired,
};

LocationDetails.defaultProps = {
  location: {},
};

export default withTranslation()(LocationDetails);
