import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import locationType from 'components/__types__/location';

const LocationFieldTable = ({ t, i18n: { language }, location }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>{t('common.name')}</TableCell>
        <TableCell>{t('common.value')}</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.id')}</span>
        </TableCell>
        <TableCell>
          <span>{location.id}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.name')}</span>
        </TableCell>
        <TableCell>
          <span>{location.name}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.address')}</span>
        </TableCell>
        <TableCell>
          <span>{location.address}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.city')}</span>
        </TableCell>
        <TableCell>
          <span>{location.city}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.postalCode')}</span>
        </TableCell>
        <TableCell>
          <span>{location.postalCode}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.notes')}</span>
        </TableCell>
        <TableCell>
          <span>{location.notes}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.location.createdDate')}</span>
        </TableCell>
        <TableCell>
          <span>
            {location.createdDate && new Date(location.createdDate).toLocaleDateString(language)}
          </span>
        </TableCell>
      </TableRow>
    </TableBody>
  </Table>
);

LocationFieldTable.propTypes = {
  location: locationType,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({
    language: PropTypes.string,
  }).isRequired,
};

LocationFieldTable.defaultProps = {
  location: [],
};

export default withTranslation()(LocationFieldTable);
